<template>
  <v-app id="inspire">
    <v-card color="grey">
      <v-toolbar dark height="65px">
        <!-- <v-btn v-on="on">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn> -->
        <div class="rotate" text icon>
          <v-icon>mdi-theme-light-dark</v-icon>
        </div>

        <v-col cols="2" md="1">
          <v-switch
            flat
            inset
            dense
            v-model="$vuetify.theme.dark"
            hide-details
            color="black"
            :src="require('./assets/dark.png')"
          />
        </v-col>
        <v-spacer></v-spacer>
        <v-col cols="7" md="4">
          <v-text-field
            color="black"
            clearable
            hide-details
            class="search"
            prepend-inner-icon="mdi-magnify"
            solo-inverted
            v-model="vsearch"
            v-on:keyup.enter="listMovies()"
          ></v-text-field>
        </v-col>

        <v-spacer></v-spacer>

        <v-card class="px-1 v-indexMenu">
          <v-navigation-drawer
            dark
            mini-variant
            mini-variant-width="50"
            permanent
          >
            <v-list>
              <v-list-item>
                <v-tooltip v-model="show" left>
                  <template v-slot:activator="{ on }">
                    <v-btn v-on="on" @click.stop="mini = !mini">
                      <v-img
                        :src="require('./assets/info.png')"
                        aspect-ratio="1"
                        width="30"
                        @click.stop="about = true"
                      ></v-img>
                    </v-btn>
                  </template>
                  <span>About</span>
                </v-tooltip>
              </v-list-item>
              <v-tooltip v-model="show2" left>
                <template v-slot:activator="{ on }">
                  <v-btn
                    v-on="on"
                    width="50"
                    height="3px"
                    icon
                    color="white"
                    @click.stop="(mini = !mini), (exit = true)"
                    >X</v-btn
                  >
                </template>
                <span>Exit</span>
              </v-tooltip>
            </v-list>
          </v-navigation-drawer>
        </v-card>
      </v-toolbar>

      <v-row justify="center">
        <v-dialog v-model="about" width="345px">
          <v-col cols="12" md="12">
            <v-card class="about">
              Infomovie was created to help find information about media content
              on the Internet (musics, movies and games);
              <p>Provided by the IMDb® API.</p>

              <v-col>
                <v-row>
                  <v-col cols="12" md="7">
                    <h6>Designed and Developed by</h6>

                    <h4>Guilherme Beitum.</h4>
                  </v-col>
                  <v-col cols="12" md="5">
                    <v-img
                      :src="require('./assets/author.jpg')"
                      class="author"
                      @click.stop="about = true"
                    ></v-img>
                  </v-col>
                </v-row>
              </v-col>
            </v-card>
          </v-col>
        </v-dialog>
      </v-row>

      <v-dialog v-model="exit" width="170px">
        <v-col cols="12" md="12">
          <v-card class="about">
            really want to leave?
            <v-col cols="12" md="12">
              <v-spacer></v-spacer>
              <v-btn class="ma-2" color="red" dark @click="closeWindow()">
                EXIT
              </v-btn>
            </v-col>
          </v-card>
        </v-col>
      </v-dialog>

      <div v-if="this.$store.state.modinfo.movies == 0" class="intro">
        <span style="color:white;">Search for musics, movies and games.</span>
      </div>

      <MovieList />

      <div v-if="this.$store.state.modinfo.movies != 0" class="text-center">
        <v-col cols="12" md="12">
          <div @click="listPage()">
            <v-pagination
              class="page"
              v-model="vpage"
              :length="leng"
              :total-visible="5"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              color="black"
              @click="listPage()"
            >
            </v-pagination>
          </div>
        </v-col>
      </div>

      <v-col></v-col>
      <v-footer app height="28" color="black">
        <span style="color:white;">&copy; InfoMovie 2020.</span>
      </v-footer>
    </v-card>
  </v-app>
</template>

<script>
import MovieList from "./components/MovieList";
import { mapGetters } from "vuex";
export default {
  name: "App",
  components: {
    MovieList,
  },
  data() {
    return {
      show: false,
      show2: false,
      about: false,
      exit: false,
      drawer: null,
      mini: true,
      vpage: 1,
      vsearch: null,
    };
  },

  computed: {
    ...mapGetters(["search, page"]),

    leng() {
      let leng = Math.round(this.$store.state.modinfo.result / 10);
      return leng;
    },

    search: {
      get() {
        return this.$store.state.search;
      },

      set(newSearch) {
        this.$store.commit("SET_SEARCH", newSearch);
      },
    },

    page: {
      get() {
        return this.$store.state.vpage;
      },

      set(newPage) {
        this.$store.commit("SET_PAGE", newPage);
      },
    },
  },
  methods: {
    closeWindow() {
      window.opener = window;
      window.close();
    },

    async listMovies() {
      this.search = this.vsearch;
      if (this.$store.state.modinfo.movies != null) {
        this.vpage = 1;
      }
      this.page = this.vpage;
      this.movies = this.$store.state.modinfo.movies;
      await this.$store.dispatch("listMovies");
    },

    async listPage() {
      this.page = this.vpage;
      this.movies = this.$store.state.modinfo.movies;
      await this.$store.dispatch("listMovies");
    },
  },
};
</script>
